import './index.scss'
import { returnHtml } from '@/lib/returnHtml'

type Props = {
  result_title: string[]
  result_contents: string[]
}

export default function ResultSummary({ result_title, result_contents }: Props) {
  return (
    <section className='un_resultSummary'>
      <div className='un_resultSummary_head'>
        <h2>結果概要</h2>
      </div>
      <div className='un_resultSummary_body'>
        <ul className='un_resultSummary_list'>
        {
          result_title.map((item, index: number) => {
            return (
              <li key={index}>
                { returnHtml(result_title[index]) }                                                                               
                {
                  returnHtml(result_contents[index])
                }
              </li>
            )
          })
        }
        </ul>
      </div>
    </section>
  )
}